$z-index-cover-base: 1;

.article {
  color: $black-medium;
  font-family: $font-light;
  padding: 20px 0;

  h2 {
    font-size: em(24, $base-font-size);
  }

  p {
    font-size: em(14, $base-font-size);
  }
}

.article-standard {
  @include span-columns(12);
}

/**
 * Article cover.
 */
.article__cover {
  background-position: top;
  height: auto;
  position: relative;
  max-height: 476px;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  z-index: $z-index-menu;

  display: flex;
  justify-content: center;
  align-items: center;
  resize: both;
  overflow: hidden;

  &:after {
    padding-top: 56.25%;
    display: block;
    content: '';
  }
}

.article__cover__content {
  @include outer-container;
  z-index: calc($z-index-menu + 2);
  position: relative;
  max-width: 550px;
  max-height: 350px;
  overflow: hidden;
  padding: 40px 15px;

  // Font.
  text-align: center;
}


.article__cover__overlay {
  height: 100%;
  width: 100%;
  background: $white;
  opacity: 0.8;
  position: absolute;
  z-index: calc($z-index-menu + 1);
}

.article__carousel {
  @include outer-container;

  // TODO(ts) - temp theming. Will be replaced later.
  text-align: center;
  height: 520px;
}
